.testimonilas {
  padding: 110px 30px;

  @include on-tablet {
    padding-top: 170px;
    padding-bottom: 170px;
  }

  @include on-desktop {
    padding-top: 170px;
    padding-bottom: 170px;
  }

  &__container {
    position: relative;
    z-index: 1;
  }

  &__container::before {
    content: '';
    position: absolute;
    inset: 0;
    background-image: url(/src/images/testimonilas/testimonilas-vector.png);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
    max-width: 233px;
    max-height: 192px;
    margin: 0 auto;
  }

  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    @include on-desktop {
      max-width: 786px;
      margin: 0 auto;
    }
  }

  &__description {
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;

    text-align: center;
    margin-bottom: 30px;
    padding-top: 50px;

    color: $main-color;

    @include on-tablet {
      font-size: 24px;
      line-height: 36px;

      padding-top: 60px;
      margin-bottom: 40px;
    }

    @include on-desktop {
      margin-bottom: 30px;
    }
  }

  &__avatar-img {
    width: 62px;
    height: 62px;

    @include hover(transform, scale(1.4));
  }

  &__name {
    font-size: 16px;
    font-weight: 700px;
    line-height: 22px;

    text-align: center;
    margin-top: 30px;

    color: $main-color;
  }

  &__role {
    font-size: 14px;
    font-weight: 400px;
    line-height: 21px;

    text-align: center;
    margin-top: 5px;

    color: $second-color;
  }
}
